import React from 'react'
import loaderStyles from './SiteLoader.module.css';
import loadersvg from '../../components/Loader/None Blue Ani.svg'
export default function SiteLoader() {
    return (
        <div className={loaderStyles["App"]}>
        <header className={loaderStyles["App-header"]}>
          <img src={loadersvg} className={loaderStyles["App-logo"]} alt="logo" />
        </header>
      </div>
    )
}
