import React from "react";
import { Link } from "react-router-dom";
// reactstrap components
import {
  Button,
  Collapse,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledTooltip,          
  UncontrolledDropdown,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  Container,
  Row,
  Col
} from "reactstrap";
import Loader from "../Loader/Loader";

class ComponentsNavbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ls:"70px",
      ani:"true",
      fill :"none",
      border :"white",
      collapseOpen: false,
      color: "navbar-transparent"
    };
  }
  componentDidMount() {
    window.addEventListener("scroll", this.changeColor);
  }
  componentWillUnmount() {
    window.removeEventListener("scroll", this.changeColor);
  }
  changeColor = () => {
    if (
      document.documentElement.scrollTop > 99 ||
      document.body.scrollTop > 99
    ) {
      this.setState({
        ls:"40px",
        ani:"false",
        fill :"white",
        border :"white",
        color: "bg-dark"
      });
    } else if (
      document.documentElement.scrollTop < 100 ||
      document.body.scrollTop < 100
    ) {
      this.setState({
        ls:"70px",
        ani:"true",
        fill :"none",
        border :"white",
        color: "navbar-transparent"
      });
    }
  };
  toggleCollapse = () => {
    document.documentElement.classList.toggle("nav-open");
    this.setState({
      collapseOpen: !this.state.collapseOpen
    });
  };
  onCollapseExiting = () => {
    this.setState({
      collapseOut: "collapsing-out"
    });
  };
  onCollapseExited = () => {
    this.setState({
      collapseOut: ""
    });
  };
  scrollToDownload = () => {
    document
      .getElementById("download-section")
      .scrollIntoView({ behavior: "smooth" });
  };
  scrollToProjects = () => {
    document
      .getElementById("project-section")
      .scrollIntoView({ behavior: "smooth" });
  };
  scrollToExperience = () => {
    document
      .getElementById("experience-section")
      .scrollIntoView({ behavior: "smooth" });
  };
 
  scrollToEducation = () => {
    document
      .getElementById("education-section")
      .scrollIntoView({ behavior: "smooth" });
  };

  scrollToSkills = () => {
    document
      .getElementById("skills-section")
      .scrollIntoView({ behavior: "smooth" });
  };
  render() {
    return (
      <Navbar
        className={"fixed-top " + this.state.color}
        color-on-scroll="100"
        expand="lg"
      >
        <Container>
          <div className="navbar-translate" id="brandlogo">
            <NavbarBrand
              to="/"
              rel="noopener noreferrer"
              tag={Link}
            > <Loader ani={this.state.ani} fill={this.state.fill} border={this.state.border} ls ={this.state.ls} />
              <span className="px-3">LAVISH KUMAR </span>
              <UncontrolledTooltip
                placement="bottom"
                target="brandlogo"
              > Designed and coded by Lavish Kumar</UncontrolledTooltip>
            </NavbarBrand>
            <button
              aria-expanded={this.state.collapseOpen}
              className="navbar-toggler navbar-toggler"
              onClick={this.toggleCollapse}
            >
              <span className="navbar-toggler-bar bar1" />
              <span className="navbar-toggler-bar bar2" />
              <span className="navbar-toggler-bar bar3" />
            </button>
          </div>
          <Collapse
            className={"justify-content-end " + this.state.collapseOut}
            navbar
            isOpen={this.state.collapseOpen}
            onExiting={this.onCollapseExiting}
            onExited={this.onCollapseExited}
          >
            <div className="navbar-collapse-header">
              <Row>
                <Col className="collapse-brand" xs="6">
                  <a href="#pablo" onClick={e => e.preventDefault()}>
                  <span className="px-3 text-white">LAVISH KUMAR </span>
                  </a>
                </Col>
                <Col className="collapse-close text-right" xs="6">
                  <button
                    aria-expanded={this.state.collapseOpen}
                    className="navbar-toggler"
                    onClick={this.toggleCollapse}
                  >
                    <i className="tim-icons icon-simple-remove" />
                  </button>
                </Col>
              </Row>
            </div>
            <Nav navbar>
            <NavItem className="p-0">
                <NavLink
                id="linkedin"
                  href="https://www.linkedin.com/in/lavish-k-6645b8112/"
                  target="_blank"
                >
                  <i className="fab fa-linkedin" />
                  <p className="d-lg-none d-xl-none">Linkedin</p>
                </NavLink>
                <UncontrolledTooltip
                placement="bottom"
                target="linkedin"
              > Follow On Linkedin</UncontrolledTooltip>
              </NavItem>
             
             
              <NavItem className="p-0">
                <NavLink
                id="github"
                  href="https://github.com/lavish-kumar"
                  target="_blank"
                >
                  <i className="fab fa-github" />
                  <p className="d-lg-none d-xl-none">Github</p>
                </NavLink>
                <UncontrolledTooltip
                placement="bottom"
                target="github"
              > Follow On Github</UncontrolledTooltip>
              </NavItem>


              <NavItem className="p-0">
                <NavLink
                id="instagram"
                  data-placement="bottom"
                  href="https://www.instagram.com/lavishkumar/"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i className="fab fa-instagram" />
                  <p className="d-lg-none d-xl-none">Instagram</p>
                </NavLink>
                <UncontrolledTooltip
                placement="bottom"
                target="instagram"
              > Follow On Instagram</UncontrolledTooltip>
              </NavItem>




              <UncontrolledDropdown nav>
                <DropdownToggle
                  caret
                  id="browsesection"
                  data-placement="bottom"
                  color="default"
                  data-toggle="dropdown"
                  href="#pablo"
                  nav
                  onClick={e => e.preventDefault()}
                >
                  <i className="fa fa-cogs d-lg-none d-xl-none" />
                  Browse Portfolio
                  <UncontrolledTooltip
                placement="bottom"
                target="browsesection"
              > Browse diffrent sections if the page</UncontrolledTooltip>
                </DropdownToggle>

                <DropdownMenu className="dropdown-with-icons dropdown-black " style={{color:"#f2f2f2"}}>
                  <DropdownItem onClick={this.scrollToProjects}>
                    <i className="tim-icons icon-app" />
                    My Projects
                  </DropdownItem>
                  <DropdownItem onClick={this.scrollToSkills}>
                    <i className="tim-icons icon-settings" />
                    My Skills
                  </DropdownItem>
                  <DropdownItem onClick={this.scrollToExperience}>
                    <i className="tim-icons icon-badge" />
                    Professional Experience
                  </DropdownItem>
                  <DropdownItem onClick={this.scrollToEducation}>
                    <i className="tim-icons icon-bullet-list-67" />
                    Education Details
                  </DropdownItem>
                  <DropdownItem tag={Link} to="/blog">
                    <i className="tim-icons icon-single-copy-04" />
                    Personal Blog
                  </DropdownItem>
                  <DropdownItem onClick={this.scrollToDownload}>
                    <i className="tim-icons icon-paper" />
                    Download CV
                  </DropdownItem>
                  <DropdownItem >
                    <i className="tim-icons icon-single-02" />
                    Contact Me
                  </DropdownItem>

                  
                </DropdownMenu>
              </UncontrolledDropdown>
              <NavItem id="viewproject">
                <Button
                  className="nav-link d-none d-lg-block"
                  color="success"
                  onClick={this.scrollToProjects}
                >
                  <i className="tim-icons icon-atom" /> View Projects
                  <UncontrolledTooltip
                placement="bottom"
                target="viewproject"
              > Have a look at some of my amazing projects.</UncontrolledTooltip>
                </Button>
              </NavItem>
              <NavItem id="downloadvc">
                <Button
                  className="nav-link d-none d-lg-block"
                  color="success"
                  href="https://drive.google.com/file/d/1ftFiIOos4eDVXNe5I4xvtZ5EKWw8i9jn/view?usp=sharing"
                  target="_blank"
                >
                  <i className="tim-icons icon-cloud-download-93" /> Download CV
                  <UncontrolledTooltip
                placement="bottom"
                target="downloadvc"
              > Download my CV for your future reference.</UncontrolledTooltip>
                </Button>
              </NavItem>
            </Nav>
          </Collapse>
        </Container>
      </Navbar>
    );
  }
}

export default ComponentsNavbar;
